<template>
  <div class="actions">
    <el-upload
      ref="uploadRef"
      :auto-upload="false"
      :on-change="handleChange"
      :on-remove="handleRemove"
      :limit="1"
    >
      <el-button type="primary">上传Har文件</el-button>
    </el-upload>
    <el-button type="success" @click="downloadAll" v-if="active"
      >打包下载</el-button
    >
  </div>
  <div class="main" v-if="active">
    <el-table :data="list" empty-text="请上传Har文件" :height="tableHeight">
      <el-table-column label="图片" :align="align" width="300">
        <template #default="scope">
          <el-image
            style="width: 100px; height: 100px"
            :src="scope.row.url"
            fit="contain"
          />
        </template>
      </el-table-column>
      <el-table-column
        prop="name"
        label="名称"
        sortable
        :align="align"
        width="300"
      >
      </el-table-column>
      <el-table-column
        prop="size"
        label="文件大小"
        sortable
        :align="align"
        width="300"
      >
        <template #default="scope">
          {{ filesize(scope.row.size) }}
        </template>
      </el-table-column>
      <el-table-column label="操作" :align="align" width="300">
        <template #default="scope">
          <el-link
            type="primary"
            :underline="false"
            :href="scope.row.url"
            target="_blank"
            >预览</el-link
          >
          <el-link
            type="success"
            :underline="false"
            @click="downloadSingle(scope.$index)"
            >下载</el-link
          >
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<style lang="less">
.actions {
  display: flex;
  flex-wrap: nowrap;
  justify-content: flex-end;
}
.actions > * {
  margin: 0 5px;
}
.el-link {
  margin-right: 5px;
}
</style>
<script>
import { ElMessage, ElMessageBox } from "element-plus";
import FileSaver from "file-saver";
import JSZip from "jszip";
import axios from "axios";
export default {
  name: "HarTable",
  data() {
    return {
      list: [],
      active: false,
      tableHeight: 500,
      align: "left",
      filename: "",
    };
  },
  mounted() {
    if (document.body.offsetWidth <= 768) {
      this.tableHeight = 300;
    }
  },
  methods: {
    // 上传更换文件
    handleChange(file) {
      let that = this;
      if (!/.har/.test(file.name)) {
        ElMessage.error("请上传Har文件");
        return false;
      }
      that.filename = file.name.replace(".har", "");
      ElMessage.warning("解析中。。。");
      var reader = new FileReader();
      reader.onload = () => {
        // console.log(e.target.result);
        let resultObject = JSON.parse(reader.result);
        let entries = resultObject.log.entries;
        let list = [];
        let urlList = [];
        entries.forEach((e) => {
          if (
            /image/.test(e.response.content.mimeType) &&
            urlList.indexOf(e.request.url) == -1
          ) {
            let url = e.request.url;
            let item = {
              url: url,
              name: decodeURIComponent(
                url.split("/")[url.split("/").length - 1].replace(/\?.*/, "")
              ),
              size: e.response.content.size,
              // base64: "data:image/png;base64," + e.response.content.text,
              raw: e,
            };
            list.push(item);
            urlList.push(url);
          }
        });
        ElMessage.success("解析完毕");
        that.list = list;
        if (list.length > 0) {
          that.active = true;
        }
      };
      reader.readAsText(file.raw);
    },
    // 移除上传文件
    handleRemove() {
      this.list = [];
      this.active = false;
      this.filename = "";
    },
    // 计算文件大小
    filesize(fileByteVar = 0) {
      var fileByte = Number(fileByteVar);
      var fileSizeMsg = "";
      if (fileByte < 1048576) fileSizeMsg = (fileByte / 1024).toFixed(2) + "KB";
      else if (fileByte == 1048576) fileSizeMsg = "1MB";
      else if (fileByte > 1048576 && fileByte < 1073741824)
        fileSizeMsg = (fileByte / (1024 * 1024)).toFixed(2) + "MB";
      else if (fileByte > 1048576 && fileByte == 1073741824)
        fileSizeMsg = "1GB";
      else if (fileByte > 1073741824 && fileByte < 1099511627776)
        fileSizeMsg = (fileByte / (1024 * 1024 * 1024)).toFixed(2) + "GB";
      else fileSizeMsg = "文件大小超过1TB";
      return fileSizeMsg;
    },
    // 使用base64格式保存图片
    getImageByBase64(image) {
      let canvas = document.createElement("canvas");
      canvas.width = image.width;
      canvas.height = image.height;
      let ctx = canvas.getContext("2d");
      ctx.drawImage(image, 0, 0, image.width, image.height);
      // 获取图片后缀名
      let extension = image.src
        .substring(image.src.lastIndexOf(".") + 1)
        .toLowerCase();
      // 某些图片 url 可能没有后缀名，默认是 png
      return canvas.toDataURL("image/" + (extension ? extension : "png"), 1);
    },
    // 下载文件
    downloadFile(url = "", options = {}) {
      return new Promise((resolve, reject) => {
        axios({
          method: "get",
          url: url,
          responseType: "blob",
          ...options,
        })
          .then((res) => {
            resolve(res.data);
          })
          .catch((error) => {
            reject(error.toString());
          });
      });
    },
    // 打包下载
    downloadAll() {
      let that = this;
      let data = this.list;
      let promises = [];
      let zip = new JSZip();
      let time = new Date().getTime();
      ElMessageBox.prompt("请输入保存文件名", "提示", {
        confirmButtonText: "确认打包",
        cancelButtonText: "取消",
        inputValue: that.filename,
        inputPattern: /^[\u4E00-\u9FA5A-Za-z0-9_]+$/,
        inputErrorMessage: "只允许中文、英文、数字和下划线",
      }).then(({ value }) => {
        ElMessage.warning("正在打包中。。。");
        let fileFolder = zip.folder(value);
        let successCount = 0;
        let errorCount = 0;
        data.forEach((e) => {
          let result = e.raw.response.headers.filter((t) => {
            return t.name == "Access-Control-Allow-Origin";
          });
          if (
            result.length == 0 ||
            result[0].value == "" ||
            result[0].value == "*"
          ) {
            // 添加时间戳，防止浏览器缓存图片
            let url = /\?/.test(e.url)
              ? e.url + "&timestamp=" + time
              : e.url + "?timestamp=" + time;
            const promise = that
              .downloadFile(url)
              .then((blob) => {
                let arr_name = e.name.split("/");
                let file_name = arr_name[arr_name.length - 1];
                // 获取文件名
                if (file_name.indexOf(".png") == -1) {
                  file_name = file_name + ".png";
                }
                fileFolder.file(file_name, blob, { binary: true });
                successCount += 1;
              })
              .catch((error) => {
                console.log(`${error} ${url}`);
                errorCount += 1;
              });
            promises.push(promise);
          }
        });
        if (promises.length > 0) {
          Promise.all(promises).then(() => {
            zip
              .generateAsync({
                type: "blob",
              })
              .then((content) => {
                ElMessage.success(
                  `打包完毕！成功${successCount}个，失败${errorCount}个`
                );
                FileSaver.saveAs(content, `${value}.zip`);
              });
          });
        } else {
          ElMessage.success("暂无可下载文件");
        }
      });
    },
    downloadSingle(index = 0) {
      console.log(index);
      const file = this.list[index];
      this.downloadFile(file.url)
        .then((res) => {
          const blob = new Blob([res], { type: res.type });
          let dom = document.createElement("a");
          let url = window.URL.createObjectURL(blob);
          dom.href = url;
          dom.download = decodeURIComponent(file.name);
          dom.style.display = "none";
          document.body.appendChild(dom);
          dom.click();
          dom.parentNode.removeChild(dom);
          window.URL.revokeObjectURL(url);
        })
        .catch((error) => {
          console.log(error);
          ElMessage.error("下载失败");
        });
    },
  },
};
</script>
